html {
    background-color: #17171d;
}

h1 {
    color: rgb(186, 191, 196);
    margin: 24px 0 8px 30px;
}

h3 {
    color: rgb(186, 191, 196);
    margin-left: 12px;
}

iframe {
    width: calc(100% - 40px);
    height: 321px;
    border: 0;
    margin: 20px 20px 20px;
    border-radius: 15px;
}

.troubleshooting {
    animation: show-ts 0s 3s forwards;
    position: absolute;
    visibility: hidden;
    width: calc(100% - 50px);
    max-width: 300px;
    top: 115px;
    left: 25px;
    height: 321px;
    color: rgb(186, 191, 196);
    z-index: -1;
}

@keyframes show-ts {
    to {
        visibility: visible;
    }
}

.all-form-inputs {
    min-width: 180px !important;
    margin-left: 30px !important;
    margin-right: 30px !important;
    margin-bottom: 12px !important;
}


.freq {
    width: 60px !important;
    margin-left: 20px !important;
    margin-right: 20px !important;
    margin-bottom: 12px !important;
    margin-top: -3px !important;
}


#full-screen {
    width: calc(100% - 40px);
    margin: 20px;
    color: rgb(186, 191, 196)
}

.big-container {
    border: 1px solid #323238;
    margin-right: 20px;
    margin-left: 20px;
    margin-bottom: 20px;
    border-radius: 12px;
}

.s-container {
    border-radius: 15px;
    border: 1px solid #31366d;
    width: fit-content;
    display: inline-block;
    margin: 12px;
    padding: 15px 15px 3px;
}

.music-text {
    margin-right: 20px;
    margin-left: 20px;
    position: relative;
    top: 6px;
}

.react-share__ShareButton {
    margin: 6px;
}

@media (min-width: 1110px) {
    .text-margin {
        height: 0;
        margin: 0;
    }
}

@media (max-width: 640px) {

    .bottom-button {
        width: 100%;
    }

    label {
        width: 100%;
    }

    .all-form-inputs {
        width: calc(100vw - 220px);
    }

    .freq {
        width: 100% !important;
        margin-left: 0 !important;
        margin-top: -15px !important;
    }

    h1 {
        text-align: center;
    }

    .song-select {
        margin-top: 20px
    }

}